html, body {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
}

input:focus{
    outline: none;
}

.community{
    display: flex;
    flex-direction: column;
    height: 100%;
    margin: 80px 0px 0px;
}

.head{
    position: fixed;
    z-index: 900;
    top: 0px;
    left: 0px;
    box-sizing: border-box;
    width: 100%;
    height: 80px;
    padding-top: var(--app-padding-top);
    background: rgb(255, 255, 255);
}

.header{
    position: relative;
    box-sizing: border-box;
    width: 1200px;
    height: 100%;
    margin: 0px auto;
}

.header-nav{
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: justify;
    justify-content: space-between;
    box-sizing: border-box;
    width: 100%;
    height: 80px;
    background: rgb(255, 255, 255);
}

.header-nav-left{
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: justify;
    justify-content: space-between;
    box-sizing: border-box;
    width: auto;
    height: 100%;
}

.header-nav-right{
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    width: auto;
    height: 40px;
}

.header-nav-right-login{
    position: relative;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: justify;
    justify-content: space-between;
    width: max-content;
    height: 24px;
    margin: 0px 20px;
}

.header-left-button{
    box-sizing: border-box;
    width: auto;
    height: 80px;
    cursor: pointer;
    margin: 0px 13px;
    padding: 19px 0px;
    font-size: 18px;
    font-weight: 400;
    color: rgb(106, 106, 106);
    background-color: rgb(255, 255, 255);
    border-bottom: none;
    border: 0;
}

.header-left-buttonactive{
    box-sizing: border-box;
    width: auto;
    height: 80px;
    margin: 0px 13px;
    padding: 19px 0px;
    font-size: 18px;
    font-weight: 700;
    color: rgb(37, 37, 37);
    background-color: rgb(255, 255, 255);
    border: 0;
    border-bottom: 3px solid rgb(37, 37, 37);
}

.header-right-button{
    width: max-content;
    user-select: none;
    -webkit-user-drag: none;
    color: rgb(106, 106, 106);
    background-color: rgb(255, 255, 255);
    font-size: 16px;
    cursor: pointer;
    padding: 0px 10px;
    box-sizing: border-box;
    border: 0;
}

.header-nav-right-write{
    cursor: pointer;
    box-sizing: border-box;
    width: 130px;
    height: 50px;
    margin: 0px 10px;
    padding: 13px 44px;
    font-size: 14px;
    font-weight: 400;
    color: rgb(255, 255, 255);
    background: rgb(0, 206, 124);
    border: none;
    border-radius: 14px;
    transition: background-color 0.3s ease-out 0s;
}

.top{
    width: 100%;
    margin: 0px auto;
}

.top-banner{
    position: relative;
    box-sizing: border-box;
    width: 100%;
    height: 341px;
    background-color: rgb(248, 248, 248);
}

.banner{
    position: relative;
    box-sizing: border-box;
    width: 100%;
    height: 341px;
    object-fit: cover;
}

.search{
    position: absolute;
    top: 221px;
    left: 50%;
    width: 588px;
    height: auto;
    margin-left: -288px;
}

.search-input{
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: justify;
    justify-content: space-between;
    box-sizing: border-box;
    width: 588px;
    padding: 20px 14px 20px 52px;
    font-size: 24px;
    font-weight: 300;
    background-color: rgb(255, 255, 255);
    border: 1px solid rgb(233, 233, 233);
    border-radius: 20px;
    box-shadow: rgba(25, 25, 25, 0.35) 0px 4px 20px 0px;
    caret-color: rgb(0, 206, 124);
}

.search-input:focus{
    border: 1px solid rgb(0, 206, 124);
}

.bar{
    height: 30px;
}

.main{
    width: 1200px;
    margin: 0px auto;
}

.option{
    position: relative;
    display: flex;
    -webkit-box-pack: justify;    
    width: 100%;
    height: auto;
}

.board-bar{
    scrollbar-width: none;
    position: relative;
    display: -webkit-box;
    -webkit-box-pack: start;
    justify-content: flex-start;
    box-sizing: border-box;
    width: auto;
    height: 40px;
    padding-right: 18px;
    transition: all 0.25s ease-out 0s;
}

.board{
    display: flex;
    gap: 22px;
    width: max-content;
    height: auto;
}

.post{
    display: grid;
    gap: 24px;
    grid-template-columns: repeat(4, 1fr);
    width: 100%;
    margin: 20px 0px 0px;
}

.post-col{
    cursor: pointer;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    width: 282px;
    height: 286px;
    background: rgb(255, 255, 255);
    border-radius: 14px;
    box-shadow: rgba(25, 25, 25, 0.2) 0px 6px 10px;
}

.post-col-head{
    position: relative;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    width: 100%;
    height: max-content;
    padding: 8px 14px;
}

.post-col-head-profile{
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    width: 50%;
    height: 100%;
}

.post-profile-img{
    overflow: hidden;
    display: flex;
    flex-shrink: 0;
    flex-wrap: nowrap;
    width: 30px;
    height: 30px;
    margin-right: 8px;
    object-fit: cover;
    border-radius: 50%;
}

.post-profile-name{
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 80px;
    height: max-content;
}

.post-profile-name-font{
    user-select: none;
    z-index: 8;
    overflow: hidden;
    display: -webkit-box;
    font-size: 12px;
    font-weight: 400;
    line-height: 18px;
    color: rgb(0, 0, 0);
    text-overflow: ellipsis;
    overflow-wrap: break-word;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
}

.post-profile-name-spec{
    display: flex;
    flex-shrink: 0;
    -webkit-box-align: center;
    align-items: center;
    box-sizing: border-box;
    width: max-content;
    height: auto;
    margin-top: 0px;
}

.post-profile-name-spec-dot{
    z-index: 8;
    display: flex;
    width: 2px;
    height: 2px;
    margin: 0px 6px;
    background: rgb(0, 143, 246);
    border-radius: 50%;
}

.post-profile-name-spec-font{
    user-select: none;
    z-index: 8;
    overflow: hidden;
    display: -webkit-box;
    font-size: 12px;
    font-weight: 400;
    line-height: 18px;
    color: rgb(0, 143, 246);
    text-overflow: ellipsis;
    overflow-wrap: break-word;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
}

.post-col-head-bar{
    display: flex;
    width: 1px;
    height: 12px;
    background: rgb(219, 219, 219);
}

.post-col-head-duration{
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
    -webkit-box-pack: center;
    justify-content: center;
    justify-self: center;
    box-sizing: border-box;
    width: 50%;
    height: 100%;
    padding-left: 18px;
}

.post-duration-bar{
    position: absolute;
    z-index: 8;
    left: 50%;
    display: flex;
    box-sizing: border-box;
    width: 0px;
    height: 100%;
    background: rgb(248, 248, 248);
    border-top-right-radius: 14px;
    transition: all 0.2s ease-out 0s;
    opacity: 0;
}

.post-duration-img{
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    box-sizing: border-box;
    width: max-content;
    height: max-content;
}

.post-duration-imgfile{
    z-index: 8;
    width: 14px;
    height: 14px;
    margin-right: 2px;
    object-fit: contain;
}

.post-duration-font{
    user-select: none;
    z-index: 8;
    display: flex;
    flex-shrink: 0;
    -webkit-box-align: center;
    align-items: center;
    box-sizing: border-box;
    font-size: 11px;
    font-weight: 400;
    line-height: 16px;
    color: rgb(154, 154, 154);
}

.post-duration-spec{
    display: flex;
    flex-shrink: 0;
    -webkit-box-align: center;
    align-items: center;
    box-sizing: border-box;
    width: max-content;
    height: auto;
    margin-top: 2px;
}

.post-duration-spec-day{
    user-select: none;
    z-index: 8;
    overflow: hidden;
    display: -webkit-box;
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
    color: rgb(0, 206, 124);
    text-overflow: ellipsis;
    overflow-wrap: break-word;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
}

.post-duration-spec-dot{
    z-index: 8;
    display: flex;
    width: 2px;
    height: 2px;
    margin: 0px 6px;
    background: rgb(0, 206, 124);
    border-radius: 50%;
}

.post-duration-spec-schedule{
    user-select: none;
    z-index: 8;
    overflow: hidden;
    display: -webkit-box;
    font-size: 12px;
    font-weight: 400;
    line-height: 18px;
    color: rgb(0, 206, 124);
    text-overflow: ellipsis;
    overflow-wrap: break-word;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
}

.post-col-img{
    position: relative;
    overflow: hidden;
    display: flex;
    width: 100%;
    height: max-content;
    border-radius: 10px;
}

.post-col-img-main{
    display: flex;
    width: 100%;
    height: 144px;
    object-fit: cover;
    transition: all 0.3s ease-out 0s;
    transform: scale(1);
}

.post-img-imgfile{
    width: 100%;
    height: 100%;
}

.post-col-img-location{
    position: absolute;
    top: 6px;
    left: 6px;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    width: max-content;
    height: 28px;
    padding: 0px 8px;
    background: rgb(248, 248, 248);
    border-radius: 10px;
}

.post-location-img{
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: center;
    justify-content: center;
    box-sizing: border-box;
    width: max-content;
    height: 20px;
    margin-right: 4px;
}

.post-location-font{
    user-select: none;
    font-size: 13px;
    font-weight: 500;
    line-height: 19px;
    color: rgb(0, 143, 246);
}

.post-col-hide{
    position: absolute;
    bottom: 0px;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: justify;
    justify-content: space-between;
    box-sizing: border-box;
    width: 100%;
    height: 0px;
    padding: 4px 10px;
    background: rgba(25, 25, 25, 0.7);
    backdrop-filter: blur(2px);
    border-radius: 0px 0px 10px 10px;
    transition: all 0.2s ease-out 0s;
    opacity: 0;
}

.post-col-hide:hover{
    height: 40px;
    opacity: 1;
}

.post-col-hide-left{
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    width: max-content;
    height: max-content;
}

.post-left-font{
    font-size: 14px;
    font-weight: 500;
    line-height: 21px;
    user-select: none;
    color: rgb(0, 206, 124);
}

.post-col-hide-right{
    display: flex;
    width: max-content;
    height: max-content;
}

.post-hide-imgfile{
    display: flex;
    width: 14px;
    height: 14px;
    margin-right: 2px;
    object-fit: contain;
}

.post-right-box{
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    width: max-content;
    height: max-content;
    margin-right: 10px;
}

.post-right-font{
    user-select: none;
    font-size: 13px;
    font-weight: 400;
    font-style: normal;
    line-height: 19px;
    color: rgb(255, 255, 255);
}

.post-col-text{
    display: flex;
