    font-size: 16px;
    font-weight: 700;
    line-height: 24px;
    color: rgb(0, 0, 0);
    text-overflow: ellipsis;
    overflow-wrap: break-word;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
}

.post-text-body{
    user-select: none;
    overflow: hidden;
    display: -webkit-box;
    margin-top: 4px;
    font-size: 15px;
    font-weight: 400;
    line-height: 22px;
    color: rgb(0, 0, 0);
    text-overflow: ellipsis;
    overflow-wrap: break-word;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
}

